@use '../../../../../../libs/shared/variables' as variables;
@mixin flex-column-center($gap: 0) {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $gap;
}

:host {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  width: 100%;
}

// === LAYOUT (Clases usadas en el template) ===
.received-payment-outer-container {
  @include flex-column-center($gap: variables.$global-light-spacing-4);
  width: 100%;
  @media (min-width: 1200px) {
    align-items: flex-start;
  }

  @media (max-width: 420px) {
    gap: variables.$global-light-spacing-2;
  }
}

@media (min-width: 1500px) {
  :host {
    justify-content: flex-start;
    align-items: flex-start;
  }
  .received-payment-outer-container {
    justify-content: flex-start;
    align-items: flex-start;
  }
}
